.services_feature {
    background-color: var(--col-primary-400);
    color: white;
    h2 {
        color: white;
    }
    &__card {
        display: grid;
        background-color: white;
        // border: 2px solid var(--col-primary-400);
        padding: 1rem;
        clip-path: polygon(17% 0%, 100% 0%, 100% 100%, 0% 100%, 0% 20%);
        height: 100%;
        color: black;
        h3 {
            border-top: 2px solid var(--col-accent-400);
            padding-top: 0.5rem;
            font-size: var(--fs-500);
            color: var(--col-primary-400);
        }
        p {
            font-size: var(--fs-200);
            color: black;
        }
        &:hover {
            p {
                color: black;
            }
            .services_feature__link::after {
                padding-left: 1rem;
                color: var(--col-primary-400);
            }
        }

    }
    &__link {
        font-family: var(--ff-title);
        font-size: var(--fs-300);
        position: relative;
        align-self: flex-end;
        &::after {
            content: "\00BB";
            position: absolute;
            padding-left: 0.5rem;
            transition: padding var(--transition), color var(--transition);
        }
    }
    &__icon {
        height: 80px;
        display: block;
        margin-bottom: 1rem;
        margin-left: auto;
    }
    
