/* inter-300 - latin */
@font-face {
    font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
    font-family: 'Inter';
    font-style: normal;
    font-weight: 300;
    src: url('../fonts/inter-v18-latin-300.woff2') format('woff2'); /* Chrome 36+, Opera 23+, Firefox 39+, Safari 12+, iOS 10+ */
}
/* inter-regular - latin */
@font-face {
    font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    src: url('../fonts/inter-v18-latin-regular.woff2') format('woff2'); /* Chrome 36+, Opera 23+, Firefox 39+, Safari 12+, iOS 10+ */
}
/* inter-600 - latin */
@font-face {
    font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
    font-family: 'Inter';
    font-style: normal;
    font-weight: 600;
    src: url('../fonts/inter-v18-latin-600.woff2') format('woff2'); /* Chrome 36+, Opera 23+, Firefox 39+, Safari 12+, iOS 10+ */
}

:root {
    font-variant-ligatures: none;
}

html {
    // font-size: var(--fs-400);
    font-size: 18px;
}

body {
    font-family: var(--ff-body);
    font-weight: var(--fw-400);
    font-size: var(--fs-400);
    line-height: 1.556;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}


h1, .h1, h2, .h2, h3, .h3, h4, .h4, .h99 {
    font-family: var(--ff-title);
    font-style: normal;
    margin-bottom: 0.5rem;
    color: var(--col-primary-400);
    text-wrap: balance;
}

h1, .h1 {
    font-size: var(--fs-900);
    font-weight: var(--fw-300);
}
h2, .h2 {
    font-size: var(--fs-700);
    font-weight: var(--fw-600);
}
h3, .h3 {
    font-size: var(--fs-600);
    font-weight: var(--fw-600);
}
h4, .h4 {
    font-size: var(--fs-500);
    font-weight: var(--fw-600);
}

.fs-100 { font-size: var(--fs-100); }
.fs-200 { font-size: var(--fs-200); }
.fs-300 { font-size: var(--fs-300); }
.fs-400 { font-size: var(--fs-400); }
.fs-500 { font-size: var(--fs-500); }
.fs-600 { font-size: var(--fs-600); }
.fs-700 { font-size: var(--fs-700); }
.fs-800 { font-size: var(--fs-800); }
.fs-900 { font-size: var(--fs-900); }

.underline {
    display: inline-block;
    padding-bottom: 0.25rem;
    border-bottom: 2px solid var(--col-accent-400);
}

strong, b {
    font-weight: var(--fw-600);
}

.fw-700 {
    font-weight: var(--fw-600);
}

.w-constrained {
    max-width: 90ch;
    margin-inline: auto
}
.w-constrained--inner > * {
    max-width: 90ch;
    margin-inline: auto
}

.has-small-font-size {
    font-size: var(--fs-200);
}

.preTitle {
    font-size: var(--fs-300);
    text-transform: uppercase;
    font-family: var(--ff-title);
    letter-spacing: 0.1em;
}

.ff-title {
    font-family: var(--ff-title);
}


.split-line {
    overflow:hidden;
    // perspective: 400;
}